import * as Yup from 'yup';
import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useSnackbar } from 'notistack5';
import { useNavigate } from 'react-router-dom';
import { FieldArray, Form, FormikProvider, getIn, useFormik } from 'formik';
import { Icon } from '@iconify/react';
import PlusFill from '@iconify/icons-eva/plus-fill';
// material
import { Delete as DeleteIcon, Save as SaveIcon } from '@material-ui/icons';
import { LoadingButton } from '@material-ui/lab';
import {
  Box,
  Card,
  Grid,
  Stack,
  TextField,
  Typography,
  FormHelperText,
  IconButton,
  CardHeader,
  CardContent,
  Button,
} from '@material-ui/core';
// utils
import { fData } from '../../utils/formatNumber';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// utils
import countries from '../../utils/constants/countries';
import { nameRegExp } from '../../utils/constants/regexs';
// components
import { UploadAvatar } from '../upload';

// ----------------------------------------------------------------------

const initialContent = {
  title: 'Basic',
  frequency: 50,
  numberOfKeys: 5,
  numberOfCoins: 5,
  numberOfStars: 5,
  numberOfBoosters: 5,
};

const NewRewardSchema = Yup.object().shape({
  avatarUrl: Yup.mixed().required('Avatar is required'),
  name: Yup.string().matches(nameRegExp, 'Cannot include special characters.').required('Name is required'),
  location: Yup.string(),
  contents: Yup.array().of(
    Yup.object().shape({
      title: Yup.string(),
      frequency: Yup.number().min(1).max(100),
      numberOfKeys: Yup.number(),
      numberOfCoins: Yup.number(),
      numberOfStars: Yup.number(),
      numberOfBoosters: Yup.number(),
    })
  ),
});

RewardNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentReward: PropTypes.object,
};

export default function RewardNewForm({ isEdit, currentReward }) {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      avatarUrl: currentReward?.avatarUrl || null,
      name: currentReward?.name || 'Easter Egg',
      location: currentReward?.location || '',
      contents: currentReward?.contents || [{ ...initialContent }],
    },
    validationSchema: NewRewardSchema,
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      alert(JSON.stringify(values));
      try {
        resetForm();
        setSubmitting(false);
        enqueueSnackbar(!isEdit ? 'Create success' : 'Update success', { variant: 'success' });
        navigate(PATH_DASHBOARD.reward.list);
      } catch (error) {
        console.error(error);
        setSubmitting(false);
        setErrors(error);
      }
    },
  });

  const { errors, values, touched, handleSubmit, isSubmitting, setFieldValue, getFieldProps } = formik;

  const handleAvatarDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];
      if (file) {
        setFieldValue('avatarUrl', {
          ...file,
          preview: URL.createObjectURL(file),
        });
      }
    },
    [setFieldValue]
  );

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item md={12}>
            <Grid container spacing={3}>
              <Grid item xs={12} md={4}>
                <Card sx={{ height: '100%' }}>
                  <CardHeader title="Reward Image/Icon" titleTypographyProps={{ variant: 'subtitle2' }} />
                  <CardContent>
                    <Box>
                      <UploadAvatar
                        accept="image/*"
                        file={values.avatarUrl}
                        maxSize={3145728}
                        onDrop={handleAvatarDrop}
                        error={Boolean(touched.avatarUrl && errors.avatarUrl)}
                        caption={
                          <Typography
                            variant="caption"
                            sx={{
                              mt: 2,
                              mx: 'auto',
                              display: 'block',
                              textAlign: 'center',
                              color: 'text.secondary',
                            }}
                          >
                            Allowed *.jpeg, *.jpg, *.png, *.gif
                            <br /> max size of {fData(3145728)}
                          </Typography>
                        }
                      />
                      <FormHelperText error sx={{ px: 2, textAlign: 'center' }}>
                        {touched.avatarUrl && errors.avatarUrl}
                      </FormHelperText>
                    </Box>
                  </CardContent>
                </Card>
              </Grid>
              <Grid item xs={12} md={8}>
                <Card sx={{ height: '100%' }}>
                  <CardContent>
                    <Stack spacing={2}>
                      <TextField
                        fullWidth
                        size="small"
                        label="Name"
                        {...getFieldProps('name')}
                        error={Boolean(touched.name && errors.name)}
                        helperText={touched.name && errors.name}
                      />

                      <TextField
                        select
                        fullWidth
                        size="small"
                        label="Location"
                        placeholder="Location"
                        {...getFieldProps('location')}
                        SelectProps={{ native: true }}
                        error={Boolean(touched.location && errors.location)}
                        helperText={touched.location && errors.location}
                      >
                        <option value="" />
                        {countries.map((option) => (
                          <option key={option.code} value={option.label}>
                            {option.label}
                          </option>
                        ))}
                      </TextField>

                      <Typography>Average value of an agg in this set in tokens = {658}</Typography>
                    </Stack>
                  </CardContent>
                </Card>
              </Grid>
            </Grid>
          </Grid>

          <Grid item md={12}>
            <FieldArray
              name="contents"
              render={(contentsArrayHelpers) => (
                <Card>
                  <CardHeader
                    title="Contents"
                    action={
                      <Button
                        variant="contained"
                        startIcon={<Icon icon={PlusFill} />}
                        onClick={() => contentsArrayHelpers.push(initialContent)}
                      >
                        Add Option
                      </Button>
                    }
                  />
                  <CardContent>
                    <Grid container spacing={1}>
                      {values.contents.map((content, contentIndex) => {
                        const fieldNamePrefix = `contents.${contentIndex}`;
                        return (
                          <Grid key={contentIndex} item xs={12} md={3}>
                            <Card>
                              <CardContent>
                                <Stack spacing={1}>
                                  <TextField
                                    fullWidth
                                    variant="standard"
                                    size="small"
                                    label="Title"
                                    {...getFieldProps(`${fieldNamePrefix}.title`)}
                                    error={Boolean(
                                      getIn(touched, `${fieldNamePrefix}.title`) &&
                                        getIn(errors, `${fieldNamePrefix}.title`)
                                    )}
                                    helperText={
                                      getIn(touched, `${fieldNamePrefix}.title`) &&
                                      getIn(errors, `${fieldNamePrefix}.title`)
                                    }
                                  />
                                  <TextField
                                    fullWidth
                                    variant="standard"
                                    size="small"
                                    label="Frequency"
                                    {...getFieldProps(`${fieldNamePrefix}.frequency`)}
                                    error={Boolean(
                                      getIn(touched, `${fieldNamePrefix}.frequency`) &&
                                        getIn(errors, `${fieldNamePrefix}.frequency`)
                                    )}
                                    helperText={
                                      getIn(touched, `${fieldNamePrefix}.frequency`) &&
                                      getIn(errors, `${fieldNamePrefix}.frequency`)
                                    }
                                  />
                                  <TextField
                                    fullWidth
                                    variant="standard"
                                    size="small"
                                    label="Number of Keys"
                                    {...getFieldProps(`${fieldNamePrefix}.numberOfKeys`)}
                                    error={Boolean(
                                      getIn(touched, `${fieldNamePrefix}.numberOfKeys`) &&
                                        getIn(errors, `${fieldNamePrefix}.numberOfKeys`)
                                    )}
                                    helperText={
                                      getIn(touched, `${fieldNamePrefix}.numberOfKeys`) &&
                                      getIn(errors, `${fieldNamePrefix}.numberOfKeys`)
                                    }
                                  />
                                  <TextField
                                    fullWidth
                                    variant="standard"
                                    size="small"
                                    label="Number of Stars"
                                    {...getFieldProps(`${fieldNamePrefix}.numberOfStars`)}
                                    error={Boolean(
                                      getIn(touched, `${fieldNamePrefix}.numberOfStars`) &&
                                        getIn(errors, `${fieldNamePrefix}.numberOfStars`)
                                    )}
                                    helperText={
                                      getIn(touched, `${fieldNamePrefix}.numberOfStars`) &&
                                      getIn(errors, `${fieldNamePrefix}.numberOfStars`)
                                    }
                                  />
                                  <TextField
                                    fullWidth
                                    variant="standard"
                                    size="small"
                                    label="Number of Coins"
                                    {...getFieldProps(`${fieldNamePrefix}.numberOfCoins`)}
                                    error={Boolean(
                                      getIn(touched, `${fieldNamePrefix}.numberOfCoins`) &&
                                        getIn(errors, `${fieldNamePrefix}.numberOfCoins`)
                                    )}
                                    helperText={
                                      getIn(touched, `${fieldNamePrefix}.numberOfCoins`) &&
                                      getIn(errors, `${fieldNamePrefix}.numberOfCoins`)
                                    }
                                  />
                                  <TextField
                                    fullWidth
                                    variant="standard"
                                    size="small"
                                    label="Number of Boosters"
                                    {...getFieldProps(`${fieldNamePrefix}.numberOfBoosters`)}
                                    error={Boolean(
                                      getIn(touched, `${fieldNamePrefix}.numberOfBoosters`) &&
                                        getIn(errors, `${fieldNamePrefix}.numberOfBoosters`)
                                    )}
                                    helperText={
                                      getIn(touched, `${fieldNamePrefix}.numberOfBoosters`) &&
                                      getIn(errors, `${fieldNamePrefix}.numberOfBoosters`)
                                    }
                                  />
                                  <IconButton
                                    color="error"
                                    aria-label="delete"
                                    onClick={() => contentsArrayHelpers.remove(contentIndex)}
                                  >
                                    <DeleteIcon />
                                  </IconButton>
                                </Stack>
                              </CardContent>
                            </Card>
                          </Grid>
                        );
                      })}
                    </Grid>
                  </CardContent>
                </Card>
              )}
            />
            <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
              <LoadingButton type="submit" variant="contained" loading={isSubmitting} endIcon={<SaveIcon />}>
                Save
              </LoadingButton>
            </Box>
          </Grid>
        </Grid>
      </Form>
    </FormikProvider>
  );
}
